html {
  background: #f1f1f1;
}

body {
  background: #f1f1f1;
}

ul {
  padding-left: 0px;
}

.layout {
  display: flex;
  flex-flow: row wrap;
  align-content: flex-start;
  font-family: Catamaran, sans-serif;
  padding: 0px;
  margin: 0px;
  background: #f1f1f1;
  box-sizing: border-box;
}

.sidebar {
  box-sizing: border-box;
  background: #100e17;
  color: #fff;
  left: 0px;
  width: 100vw;
  z-index: 1;
  display: inline-block;
  overflow: hidden;
}

@media screen and (min-width: 1152px) {
  .sidebar {
    position: fixed;
    top: 0px;
    bottom: 0px;
    width: 30%;
    display: inline-block;
  }
}

.header {
  margin: 1em;
  text-align: center;
  position: relative;
  z-index: 2;
}

@media screen and (min-width: 1152px) {
  .header {
    margin: 35vh 2em 0 2em;
    text-align: right;
    border-radius: 8px;
  }
}

.header::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  box-shadow: inset 0 0 60px 10px rgba(255, 255, 255, 0.5);
  margin: -40px;
  border-radius: 8px;
}

@media screen and (min-width: 1152px) {
  .header::before {
    box-shadow: inset 0 0 60px 10px rgba(255, 255, 255, 0.35);
  }
}

.content {
  padding: 1em;
  vertical-align: baseline;
  width: 100%;
  box-sizing: border-box;
}

@media screen and (min-width: 1152px) {
  .content {
    padding-left: 2em;
    padding-right: 2em;
    width: 70%;
    margin-left: 30%;
    vertical-align: top;
  }
}

.author {
  color: white;
  transition: all ease 250ms;
}

.author:hover {
  text-shadow: 0px 0px 10px rgba(80, 180, 255, 0.5);
  transition: all ease 250ms;
}

.site-title {
  font-size: 3em;
  margin: 0;
  text-transform: uppercase;
  z-index: 1;
}

.subtitle {
  margin: 0;
  font-size: 1.5em;
  font-weight: normal;
}

li {
  margin: 0px;
}

.nav {
  ul {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    align-content: center;
    margin-top: -0.5em;
    margin-left: -0.5em;
  }
  ul > li {
    margin-top: 0.5em;
    margin-left: 0.5em;
    margin-right: 0.5em;
  }
}

@media screen and (min-width: 1152px) {
  .nav {
    ul {
      justify-content: flex-end;
    }
  }
}

.nav-button {
  display: block;
  background: transparent;
  box-sizing: border-box;
  border: 2px solid rgb(176, 202, 219);
  border-radius: 2px;
  text-transform: uppercase;
  font-size: 1em;
  color: #fff;
  user-select: none;
  cursor: pointer;
  padding: 0.33em 1em 0.33em 1em;
  letter-spacing: 0.1em;
  white-space: nowrap;
  position: relative;
  transition: all ease 250ms;
  border-radius: 4px;
}

@media screen and (min-width: 1152px) {
  .nav-button {
    margin-left: 0px;
    margin-right: 0px;
  }
  .nav ul > li {
    margin-top: 1em;
    margin-left: 1em;
    margin-right: 0em;
  }

  .nav ul > li:first-child {
    margin-left: 0em;
  }
}

.nav ul li:first-child .nav-button {
  margin-left: 0px;
}

.nav ul li:last-child .nav-button {
  margin-right: 0px;
}

.nav-button:hover {
  border: 2px solid rgb(80, 180, 255) !important;
  text-decoration: none;
  transition: all ease 250ms !important;
  box-shadow: 0 0 10px 2px rgba(80, 180, 255, 0.5);
}

.search-form {
  margin-top: 1em;
}
.submit-button {
  margin-left: 4px;
}
.tags {
  font-size: 1.5em;
  text-transform: uppercase;
  margin-top: 1em;
}

.blog,
.author {
  transition: all ease 250ms;
}

.blog:hover,
.author:hover {
  color: rgb(80, 180, 255) !important;
  transition: all ease 250ms;
}

a,
a :visited :active {
  color: #4090d0;
  text-decoration: none;
  transition: all ease 250ms;
}

a:hover {
  transition: all ease 250ms !important;
  color: rgb(80, 180, 255);
}

.page-header {
  border-bottom: 1px solid #666;
  color: #666;
  font-size: 1em;
  margin-bottom: 0.5em;
  margin-top: 0.5em;
  padding-bottom: 0.25em;
  text-transform: uppercase;
  letter-spacing: 0.25em;
}

@media screen and (min-width: 1152px) {
  .page-header {
    margin-bottom: 1.5em;
    margin-top: 1.5em;
  }
}

@media screen and (min-width: 1152px) {
  .title-container {
    display: flex;
    flex-direction: row;
  }
}

.post-title {
  color: #4a4a4a;
  word-wrap: normal;
  word-break: keep-all;
  hyphens: none;
  font-weight: normal;
  margin-bottom: 0.2em;
  margin-top: 0px;
  margin-right: 0.5em;
  width: fit-content;
  transition: all ease 250ms;
}

.post-title:hover {
  color: rgb(80, 180, 255);
  transition: all ease 250ms;
}

.post {
  max-width: 800px;
  background: #ffffff;
  padding-left: 2em;
  padding-right: 2em;
  padding-bottom: 3em;
  padding-top: 1em;
  margin-top: 2em;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 2em;
  box-shadow: 6px 12px 20px rgba(39, 44, 49, 0.12),
    1px 6px 10px rgba(39, 44, 49, 0.06);
  transition: all ease 250ms;
  border-radius: 4px;
}

.post:hover {
  transition: all ease 250ms;
  box-shadow: 0px -0px 16px 4px rgba(80, 180, 255, 0.5);
}

.post-info {
  display: flex;
  flex-direction: row;
  vertical-align: middle;
}

.post-metadata {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-end;
  .tag-link {
    margin-top: 0px;
  }
}

@media screen and (min-width: 1152px) {
  .post-metadata {
    align-items: flex-start;
  }
}

.posts-container {
  display: block;
}

.author-image {
  max-width: 48px;
  max-height: 48px;
  border-radius: 48px;
  vertical-align: middle;
}

.author-image-div {
  display: block;
  text-align: right;
  margin-left: auto;
  margin-right: 0px;
}

.post-header {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.post-image {
  display: block;
  margin-bottom: 1em;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 6px 12px 20px rgba(39, 44, 49, 0.12),
    1px 6px 10px rgba(39, 44, 49, 0.06);
}

.post-preview {
  min-height: fit-content;
  height: 5em;
  overflow: hidden;
  margin-top: -5em;
}

.read-more-div {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 5em;
  margin: 0px;
  text-align: center;
  align-content: flex-end;
  background-image: linear-gradient(to bottom, transparent, white);
}

.read-more {
  transition: all ease 250ms;
  margin-top: 5em;
  height: fit-content;
  width: fit-content;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  color: white;
  padding: 1%;
  color: #5080b0;
  overflow: hidden;
}

.read-more:hover {
  color: rgb(80, 180, 255);
  transition: all ease 250ms;
}

.title-container-nested {
  height: fit-content;
}

.tag-container,
.blog-post-tags-container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: flex-start;
  justify-content: flex-start;
}

@media screen and (max-width: 374px) {
  .tag-container,
  .blog-post-tags-container {
    flex-direction: row;
    flex-wrap: wrap;
    a {
      margin-left: 0.5em !important;
      margin-top: 0.5em !important;
    }
  }
}

@media screen and (min-width: 1152px) {
  .tag-container,
  .blog-post-tags-container {
    justify-content: flex-end;
    align-items: center;
  }
}

.tag-container a,
.blog-post-tags-container a {
  margin: 0px;
  margin-left: 1em;
  flex-basis: auto;
  flex-grow: 0;
  flex-shrink: 1;
  width: fit-content;
  height: fit-content;
}

.tag-container a:first-child,
.blog-post-tags-container a:first-child {
  margin-left: 0px;
}

.tag-link {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  transition: all ease 250ms !important;
  background: #5080b0;
  margin-bottom: 0px;
  padding: 0.333em;
  padding-left: 0.5em;
  padding-right: 0.5em;
  color: white;
  max-width: fit-content;
  border-radius: 4px;
  height: fit-content;
}

.tag-link svg {
  margin-right: 4px;
}

.tag-link:hover {
  color: white;
  background: rgb(80, 180, 255) !important;
  transition: all ease 250ms !important;
}

.tag-link .tag-total-count {
  margin-left: 0.5em;
}
.author-link {
  vertical-align: baseline;
  margin-top: auto;
  margin-bottom: 1.333em;
  color: #5080b0;
}

.date {
  margin-top: 1em;
}

@media screen and (min-width: 1152px) {
  .date {
    margin-top: 0px;
  }
}

.date-string {
  margin-left: auto;
  margin-right: auto;
  max-width: fit-content;
  text-align: center;
}

.title {
  text-align: center;
}

.gatsby-highlight-code-line {
  background-color: #ddeeff;
  display: block;
  margin-right: -1em;
  margin-left: -1em;
  padding-right: 1em;
  padding-left: 0.75em;
  border-left: 0.25em solid rgb(175, 220, 255);
}
.gatsby-highlight {
  background-color: #f1f1f1;
  border-radius: 0.3em;
  margin: 0.5em 0;
  padding: 1em;
  overflow: auto;
}

.gatsby-highlight pre[class*="language-"] {
  background-color: transparent;
  margin: 0;
  padding: 0;
  overflow: initial;
  float: left;
  min-width: 100%;
}

.gatsby-highlight pre[class*="language-"].line-numbers {
  padding-left: 1.4em !important;
}

.command-line-prompt > span:before {
  color: #999;
  content: " ";
  display: block;
  padding-right: 0.8em;
}

.command-line-prompt > span[data-user]:before {
  content: "[" attr(data-user) "@" attr(data-host) "] $";
}

.gatsby-highlight code {
  padding-left: 1em !important;
}

.tags-list {
  list-style: none;
}

.blog-post-meta-date {
  margin-right: 0.5em;
  font-size: x-large;
}

@media screen and (min-width: 1152px) {
  .blog-post {
    margin-left: 1em;
    margin-right: 1em;
  }
}

.blog-post-time-to-read {
  font-weight: normal;
  font-size: 16px;
  margin-top: 1em;
  margin-bottom: 1em;
}

@media screen and (min-width: 1152px) {
  .blog-post-time-to-read {
    margin-bottom: 2em;
  }
}

.blog-post-image {
  display: block;
  margin-bottom: 1em;
  width: 100%;
  max-width: 960px;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 6px 12px 20px rgba(39, 44, 49, 0.12),
    1px 6px 10px rgba(39, 44, 49, 0.06);
}

.social-links {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  align-content: center;
  margin-top: 0.75em;
  margin-left: auto;
  margin-right: auto;
  max-width: fit-content;
  .social-link {
    margin-left: 1em;
    svg {
      color: white;
      fill: white;
      transition: all ease 250ms;
      height: 24px;
    }
    svg:hover {
      filter: drop-shadow(0px 0px 8px rgba(80, 180, 255, 1));
      transition: all ease 250ms;
    }
  }
  .social-link:first-child {
    margin-left: 0px !important;
  }
}

@media screen and (min-width: 1152px) {
  .social-links {
    margin-left: auto !important;
    margin-right: 0px;
    margin-top: 1.25em;
    .social-link {
      .social-link-icon {
        height: 32px;
      }
    }
  }
}

.post-navigator {
  display: flex;
  width: 100%;
  .post-navigator-next {
    margin-left: auto;
    margin-right: 0px;
    transition: all ease 250ms;
    .post-navigator-link-div {
      justify-content: space-evenly;
    }
  }
  .post-navigator-previous {
    margin-right: auto;
    margin-left: 0px;
    .post-navigator-link-div {
      justify-content: space-evenly;
    }
  }
  .post-navigator-link-div {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    p {
      margin: 0px;
      padding: 0px;
      margin: 0.25em;
      margin-top: 0px;
      margin-bottom: 0px;
      font-size: larger;
    }
    svg {
      margin: 0px;
    }
  }
}

.tags-list p {
  margin-bottom: 0px;
}

@media screen and (min-width: 1152px) {
  .blog-post-tags-container {
    margin-top: 0px;
  }
}

.gatsby-resp-image-wrapper {
  box-shadow: 6px 12px 20px rgba(39, 44, 49, 0.12),
    1px 6px 10px rgba(39, 44, 49, 0.06);
}

.tl-edges {
  overflow-x: visible;
}

.blog-post-content[data="author"] {
  .gatsby-resp-image-wrapper {
    max-width: 480px !important;
    border-radius: 50%;
    overflow: hidden;
    box-sizing: border-box;
  }
}

ul {
  margin-bottom: 0px;
}

.tags-list {
  margin-bottom: 1em;
}

.leancloud-visitors {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  .post-meta-item-text {
    font-style: normal;
    font-size: smaller;
    font-weight: 400;
    margin-left: auto;
    margin-right: 0.5em;
  }
  .leancloud-visitors-count {
    margin-right: auto;
    font-style: normal;
    font-weight: 400;
    font-size: smaller;
  }
}

.v .vwrap {
  background: white;
  border-radius: 6px !important;
  color: dimgray;
  border-color: dimgray;
  button {
    border-radius: 6px;
  }
  .vheader * {
    border-bottom: 1px solid lightgray !important;
    color: dimgray !important;
  }
  .vedit textarea {
    color: dimgray !important;
    font-family: sans-serif;
  }
  .vbtn {
    border-color: dimgray !important;
    color: dimgray !important;
  }
  * {
    color: dimgray !important;
    border-color: dimgray !important;

    fill: dimgray !important;
  }
}

.v .vempty {
  display: none !important;
}

.v .vcontent p {
  color: dimgray !important;
}

::selection {
  background-color: rgba(128, 198, 255, 0.6);
}
